/* uri*/
export function validateURL(textval) {
  const urlregex = /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/
  return urlregex.test(textval)
}

/* */
export function validateLowerCase(str) {
  const reg = /^[a-z]+$/
  return reg.test(str)
}

/* */
export function validateUpperCase(str) {
  const reg = /^[A-Z]+$/
  return reg.test(str)
}

/* */
export function validatAlphabets(str) {
  const reg = /^[A-Za-z]+$/
  return reg.test(str)
}


export function checkMobile(rule, value, callback){
  var partten = /^(13[0-9]|14[0-9]|15[0-9]|16[6]|18[0-9]|19[6,9]|17[0-9])\d{8}$/i;
  if (!value) {
    callback(new Error(""));
  } else if (partten.test(value)) {
    callback();
  } else {
    callback(new Error(""));
  }
};//
export function checkEmail(rule, value, callback){
  if (!value) {
    return callback();
  }
  if (value) {
    setTimeout(() => {
      var reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if (!reg.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    }, 500);
  }
};//
//
export function inputTrim(rule,value,callback){
  let string="";
    if(value){
      string=value.replace(/\s*/g,'');
    }
    if(string){
      return callback();
    }else{
      return callback("")
    }
}
//
export function validateAcquaintance(rule, value, callback){
  if (!value) {
    callback(new Error(""));
  }
  value = Number(value);
  if (typeof value === "number" && !isNaN(value)) {
    callback();
  } else {
    callback(new Error(""));
  }
};
//
export function validatorPrice(rule, value, callback){
  var reg = /^\d{1,9}(?:\.\d{1,2})?$/;
  if (reg.test(value)) {
    callback();
  } else {
    callback(new Error(""));
  }
};



