import axios from 'axios'

import Vue from 'vue'
import router from '@/router'
import store from '@/store/index'

const request = {
  get,post,postd,postAll,getAll
};
var boolUrl=false;
if(boolUrl){
  var localUrl='http://localhost:64139/api/';
  var shopTest='../../api/';
}else{
  var localUrl='http://lelTest.moguxia.cn/api/';
  var shopTest='http://lelshopTest.moguxia.cn/api/';
}
axios.interceptors.request.use(
	config => {
		config.headers['Authorization'] = `bearer ${localStorage.getItem('tokens')}`;
		return config;
	},

	err => {
		return Promise.reject(err);
	}
);

function genQueryString(obj) {
	let data = [];
	Object.keys(obj).map(key => {
		let param = `${key}=${obj[key]}`;
		data.push(param);
	});
	return data.join('&');
}

function get(module, query) {

  let url = module;
  if(query){
    let queryString = genQueryString(query);
    if (queryString) url += '?' + queryString;
  }

	return new Promise(resolve => {
    axios.defaults.baseURL = localUrl;
		axios.get(url).then(res => {
			if (res.data && res.status === 200) {
			    if (res.data.code !== 0) {
			        resolve(null);
			    } else {
					res.data.msg === 'SUCCESS'? resolve(res.data.content): resolve(res.data.content);
			    }
			} else {
        Vue.prototype.$Message.error('');
			    resolve(null);
			}
		}).catch(err => {
      if (err.response.status === 401) {
				Vue.prototype.$Message.error('');
				// store.dispatch('signOut');
        store.dispatch('LogOut');
				router.push('/login');
			} else {
				Vue.prototype.$Message.error('');
			}

			resolve(null);
		});
	});
}

function post(module, query,data) {
	let url = module;
  if(query){
    let queryString = genQueryString(query);
    if (queryString) url += '?' + queryString;
  }
    return new Promise(resolve => {
      axios.defaults.baseURL = localUrl;
        axios.post(url,data).then(res => {
            if (res.data && res.status === 200) {
                if (res.data.code !== 0) {
                    resolve(res.data.content);
                } else {
                	res.data.msg === 'SUCCESS'? resolve(res.data.content): resolve(null);
                }
            } else {
              Vue.prototype.$Message.error('');
                resolve(null);
            }
        }).catch(err => {
          if (err.response.status === 401) {
            Vue.prototype.$Message.error('');
            // store.dispatch('signOut');
            store.dispatch('LogOut');
            router.push('/login');
          } else {
            Vue.prototype.$Message.error('');
          }
            resolve(null);
        });
    });
}
