function getAll(module, query) {
  let url = module;
  if(query){
    let queryString = genQueryString(query);
    if (queryString) url += '?' + queryString;
  }

  return new Promise(resolve => {
    axios.defaults.baseURL = localUrl;
    axios.get(url).then(res => {
      if (res.data && res.status === 200) {
        if (res.data.code !== 0) {
          resolve(null);
        } else {
          res.data.msg === 'SUCCESS'? resolve(res.data): resolve(null);
        }
      } else {
        Vue.prototype.$Message.error('');
        resolve(null);
      }
    }).catch(err => {
      Vue.prototype.$Message.error('');
      resolve(null);
    });
  });
}

// if(query instanceof Array){}
function postAll(module, query,data) {
  let url = module;
  if(query){
    let queryString = genQueryString(query);
    if (queryString) url += '?' + queryString;
  }
  return new Promise(resolve => {
    axios.defaults.baseURL = localUrl;
    axios.post(url,data).then(res => {
      if (res.data && res.status === 200) {
        if (res.data.code !== 0) {
          resolve(null);
        } else {
          res.data.msg === 'SUCCESS'? resolve(res.data): resolve(null);
        }
      } else {
        Vue.prototype.$Message.error('');
        resolve(null);
      }
    }).catch(err => {
      Vue.prototype.$Message.error('');
      resolve(null);
    });
  });
}

export default request;



